/* global window */
import React from 'react';
import { compose, withState } from 'recompose';
import moment from 'moment';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography, Grid, Paper
} from '@material-ui/core';
import PropTypes from 'prop-types';
import { get as _get, keys as _keys, map as _map } from 'lodash';
import { USER_MANAGER_ROLES } from '../../enums/user-roles';
import withNotAuthorizedHandler from '../../utils/with-not-authorized-handler';
import Loading from '../../components/Loading';
import Header from '../../components/Header';
import AlertBox from '../../components/AlertBox';
import withRequest from '../../utils/with-request';
import ServiceRequestsService from '../../services/service-request';
import styles from './styles';

const MyRequestDetails = ({
  classes, serviceRequestData, serviceRequestLoading,
  setAlert, alert
}) => {
  const { showAlert, alertMsg, alertType } = alert;
  const onClearAlert = () => setAlert({ alertMsg: '', alertType: '', showAlert: false });
  let alertBox = null;
  if (showAlert) {
    alertBox = <AlertBox
      message={alertMsg}
      type={alertType}
      onClose={onClearAlert}
      actionButton={<b>Recarregar</b>}
      onClickAction={() => window.location.reload()}
    />;
  }
  const serviceData = _get(serviceRequestData, 'service_request.service_data', '');
  const formQuestions = _get(serviceData, 'form_questions', '');
  const formQuestionsKeys = _keys(formQuestions);
  return (
    <div className={classes.root}>
      <Header alertBox={alertBox} />
      { serviceRequestLoading ?
        <Loading />
        :
        <React.Fragment>
          { serviceData ?
            <section>
              <div className={classes.container}>
                <div className={classes.headerView}>
                  <aside>
                    <Typography variant="h5">
                      <b>{serviceData.title}</b>
                    </Typography>
                  </aside>
                </div>
              </div>
              <Grid container className={classes.container}>
                <Grid item xs={12} md={5}>
                  <p className={classes.serviceSubtitle}><b>{serviceData.subtitle}</b></p>
                  <p className={classes.serviceDescription}>{serviceData.description}</p>
                </Grid>
                <Grid item xs={12} md={8}>
                  <Paper className={classes.paperContainer}>
                    <span className={classes.paperDateInfo}>{`Enviado em ${moment(serviceData.created_at).format('DD/MM/YYYY')}`}</span>
                    <React.Fragment>
                      {_map(formQuestionsKeys, (formQuestion) => {
                        return (
                          <div key={formQuestion}>
                            <p className={classes.paperTitle}>
                              <b>{formQuestion}</b>
                            </p>
                            <p className={classes.paperSubtitle}>
                              {_get(formQuestions, formQuestion, '')}
                            </p>
                          </div>
                        );
                      })}
                    </React.Fragment>
                  </Paper>
                </Grid>
              </Grid>
            </section>
            :
            ''}
        </React.Fragment>}
    </div>
  );
};

const withServiceRequest = withRequest(ServiceRequestsService.getServiceRequest, {
  name: 'serviceRequest',
  options: ({ setAlert, match }) => ({
    variables: (_get(match, 'params.id')),
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar o pedido',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

MyRequestDetails.propTypes = {
  classes: PropTypes.object,
  serviceRequestData: PropTypes.object,
  serviceRequestLoading: PropTypes.bool,
  alert: PropTypes.object,
  setAlert: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('alert', 'setAlert', {
    alertMsg: '',
    alertType: '',
    showAlert: false
  }),
  withServiceRequest,
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.member.role)
  })
)(MyRequestDetails);
