import React from 'react';
import {
  FormControl,
  FormHelperText,
  TextField
} from '@material-ui/core';
import { isEmpty as _isEmpty, isArray as _isArray } from 'lodash';
import PropTypes from 'prop-types';
import styles from './styles';

const FormTextField = ({
  errors, variant = 'outlined', InputLabelProps = { shrink: true },
  maskFunction, value, onChangeText = () => {},
  ...otherProps
}) => {
  return (
    <FormControl fullWidth margin="normal" error={!_isEmpty(errors)}>
      <TextField
        error={!_isEmpty(errors)}
        InputLabelProps={InputLabelProps}
        variant={variant}
        value={value}
        onChange={event => {
          const newValue = event.target.value;
          if (!maskFunction || (value && newValue.length < value.length)) {
            return onChangeText(newValue);
          }

          const handledNewValue = maskFunction(newValue);
          if (handledNewValue !== newValue) {
            onChangeText(handledNewValue);
          } else {
            onChangeText(handledNewValue);
          }
          return null;
        }}
        {...otherProps}
      />
      {!_isEmpty(errors) && <FormHelperText style={styles.errors}>{_isArray(errors) ? errors.join(', ') : errors}</FormHelperText>}
    </FormControl>
  );
};

FormTextField.propTypes = {
  errors: PropTypes.any,
  variant: PropTypes.string,
  InputLabelProps: PropTypes.shape({
    shrink: PropTypes.bool
  }),
  maskFunction: PropTypes.func,
  value: PropTypes.any,
  onChangeText: PropTypes.func,
  className: PropTypes.any
};

export default FormTextField;
